<script>
import VueButton from 'components/button'
import html2canvas from 'plugins/html2canvas/html2canvas'
import Icon from 'vue-awesome/components/Icon.vue'
import 'vue-awesome/icons/close'
import 'vue-awesome/icons/text-height'
import 'vue-awesome/icons/chevron-left'
import move from 'components/move'
import {mapGetters} from 'vuex'
import { swiper, swiperSlide } from 'vue-awesome-swiper'

export default {
	data () {
		return {
			buttons:[], //
			hide: true, //
			previewsrc:'', //
			leave:'', //
			isShowTool: false,  //
			swiperOption: {
		        autoHeight: true,
		        slidesPerView: 5,
		        freeMode: true,
		        onTransitionStart(swiper){

		        },
	      }
		}
	},
	props:{
		src: {
			type:String,
			default: null
		}
	},
	render :function(createElement) {
		return createElement(
			'div',//
			{//
				class: {'edit-container':true},
				on:{
					click:this.cancel
				}
			},
			//
			[	
				createElement( //
					'section',
					{
						class:{editimg:true},
					},
					[
						createElement(
							'div',
							{class:{editing:true},ref: 'editimg'},
							[createElement(
								'img',
								{
									domProps:{src: this.src}
								}
							),
							this.elements.map( (com)=> {
								return createElement(
									'move',
									{
										class:{text:true},
										attrs: {
											top: com.top,
											left: com.left,
											color: com.color,
											id: com.id,
											isShowTool: this.isShowTool,
											name:com.id,
											text: com.text
										},
										on: {
											editElem:this.editElem,
											deleteElem:this.deleteElem,
											showTool: this.showTool
										}
									}
								)
							})]
						)
					]
				),
				//
				createElement( 
					'section',
					{
						class:{edittool:true}
					},
					[
						createElement(
							'swiper',
							{
								attrs: {
									options:this.swiperOption
								},
								ref: "mySwiperA"
							},
							this.buttons.map((button) =>{
								return createElement(
									'swiper-slide',
									[
										createElement(
											'v-button',
											{
												on:{
													'!click':this.addComponent
												},
												attrs: {
													btntype:button.type,
													btnClass:button.class
												}
											},
											[
												createElement('icon',{attrs:{ name: button.icon}}),
												createElement('span',
													{
														class:{undertext : true},
														domProps: {innerHTML: button.name},
														slot:'undertext'
													},
												),
											]
										)
									]
								)
							})
						)
					]
				),
				//
				createElement(
					'section',
					{
						class:{sumitcontainer:true}
					},
					[
						createElement(
							'v-button',
							{
								on:{click: this.change},
								attrs: {btntype: "success",btnClass:'v-btn'},
							},
							[createElement('span','')]
						)
					]
				),
				//
				createElement(
					'div',
					{
						class:{preview: true,hide:this.hide,leave:this.leave},
						ref: "preview"
					},
					[
						createElement(
							'div',
							{on:{click:this.close},class:{close:true}},
							[
								createElement('icon',{attrs:{name:'close'}})
							]
						),
						createElement(
							'img',
							{attrs:{src:this.previewsrc}}
						)
					]
				)
			]
		)
	},
	components: {
		'v-button':VueButton,
		'icon': Icon,
		'swiper': swiper,
		'swiper-slide': swiperSlide,
		move,
	},
	created (){
		this.addBtn();
	},
	computed: {
		...mapGetters({
			'elements': 'getElements',
			'elemLength': 'getLength'
		})
	},
	methods: {
		//
		cancel (e) {
			let target = e.target;
			/*
			 * v-touch
			 * movebox 
			 */
			while(target && target.classList && !target.classList.contains('movebox')) {
				target = target.parentNode;
			}
			if(!(target.classList && target.classList.contains('movebox'))) this.isShowTool = false;
		},
		//
		showTool (e) {
			this.isShowTool = true;
		},
		//
		change (e) {
			e.stopPropagation();
			this.isShowTool = false;
			html2canvas(this.$refs.editimg).then((canvas) => {
				this.previewsrc = canvas.toDataURL("image/jpeg",1);
				this.leave = false;
				this.hide = false;
			})
			
		},
		//
		addComponent (e) {
			e.stopPropagation();
			this.isShowTool = false;

			const type = e.currentTarget.dataset['type'];
			if(type == "back"){ //
				this.$emit('back');
			}else if(type == "text"){ //
				let obj = {
					id : Math.random().toString().substr(2, 8),
					top: this.elemLength * 10 + 5,
					left: this.elemLength + 2,
					text: '',
					color: 'rgba(25,77,51,1)'
				}
				this.$store.dispatch('addElem',obj);
			}
		},
		//
		addBtn () {
			let btns = [{
				icon: 'chevron-left',
				type: 'back',
				class: 'back'
			},{
				icon:'text-height',
				name: '',
				type: 'text',
				class: 'v-btn'
			}]
			this.buttons = btns;
		},
		//
		close () {
			this.leave = true;
			setTimeout( () =>{this.hide = true;},1*1000)
		},
		//
		editElem (id){
			this.$store.dispatch('setElem',{id:id});
			this.$router.push('/edittext');
		},
		//
		deleteElem (id){
			this.$store.dispatch('deleteElem',{id:id})
		}
	}
}
</script>
<style lang="scss">
@import 'scss/editimg';
</style>


