// Vue
import Vue from 'vue'
//  src/App.vue  
import App from './App'
// 
import VueRouter from 'vue-router'
// 
import router from './config/routes'
//
import filter from './filter/filter'
//
// import VueResource from 'vue-resource' --v-resource
import axios from 'axios'
import VueAxios from 'vue-axios'
//
import VueTouch from 'vue-touch'
//Vuex state
import store from './store/index'
import api from './config/api'
//
Vue.use(VueRouter);
//
// Vue.use(VueResource);
Vue.use(VueAxios, axios);
//touch
Vue.use(VueTouch);
//filter
Vue.use(filter);
Vue.use(api);
//vuex
//Vue.use(Vuex);
new Vue({
  router,
  store,
  el: '#app',
  render:(h) => h(App),
  created:function(){
  	console.log("created");
  }
})



